  .main-nav li {
    font-size: 2rem;
    margin-bottom: 2rem; }
    .main-nav li .main-nav__link {
      display: inline-block;
      position: relative;
      z-index: 3; }
      .main-nav li .main-nav__link:before {
        content: '';
