        position: absolute;
        left: 0;
        bottom: -5px;
        width: 60%;
        height: 10px;
        z-index: 1;
        -webkit-transition: all 0.4s;
        transition: all 0.4s; }
      .main-nav li .main-nav__link:hover:before {
        width: 100%; }
    .main-nav li:nth-child(1) .main-nav__link:before {
      background-image: -webkit-gradient(linear, left top, right top, from(#053765), to(#035F53));
